.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  margin: 20 auto;
}

.info {
  width: 300px;
  margin: 0 auto;
  display: flex;
  font-variant: small-caps;
}

.info-item {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-form {
  width: 250px;
  display: block;
  margin: 20px auto 0;
}

.load-more {
  width: 100px;
  font-variant: small-caps;
  display: block;
  margin: 0 auto 20px;
}

.photo-link:hover {
  cursor: zoom-in;
}
